body {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 0.7rem;
  text-align: center;
  color: #eee;
  background-color: #e3e8ed; }

.trailer {
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.95);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: display .2s; }
  .trailer .iframe-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 100%;
    height: 100%; }
    .trailer .iframe-wrapper iframe {
      width: 90%;
      height: 90%;
      border: 0; }

.trailer-close-btn {
  position: fixed;
  top: 0;
  right: 0;
  font-size: 3.5rem;
  margin: 0 2rem;
  cursor: pointer; }

.modal-container {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  margin-left: 350px;
  z-index: 1; }

.modal {
  display: flex;
  text-align: justify;
  align-items: stretch;
  width: calc(36% + 500 * 0.6667px);
  max-width: calc($max-modal-width + 500 * 0.6667px);
  height: 500;
  max-height: 500;
  margin: 8% 13%;
  padding: 0;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 20px 0px rgba(32, 41, 50, 0.7); }

.modal-image {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-size: cover;
  background-position: center;
  height: 500px;
  width: 333.35px;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem; }

.modal-trailer {
  height: 140px;
  width: 400px;
  margin: 0 auto;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: height .2s; }

.modal-trailer:hover {
  height: 160px;
  cursor: pointer; }

.modal-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #e3e8ed;
  color: #161c22;
  max-width: 700px;
  border-top-right-radius: 0.6rem;
  border-bottom-right-radius: 0.6rem; }

.modal-header {
  background-color: #202932;
  background: linear-gradient(to top, #2d4358 0%, #205a65 100%);
  color: #e3e8ed;
  padding: 1rem 2rem;
  border-top-right-radius: 0.5rem; }
  .modal-header h1 {
    font-size: 2.2rem;
    margin: 0;
    font-weight: normal; }
  .modal-header h2 {
    font-size: 1.3rem;
    margin: .5rem 0 0 0;
    font-weight: normal; }

.modal-close-btn {
  float: right;
  font-size: 3.5rem;
  margin-left: 1rem;
  cursor: pointer; }

.modal-close-btn:hover {
  cursor: pointer; }

.modal-year {
  color: #869cb2; }

.modal-plot {
  font-size: 1.3rem;
  line-height: 2rem;
  padding: .5rem 5rem .5rem 1.5em;
  width: 90%; }

.modal-facts {
  font-size: 1.3rem;
  padding: 0rem 5rem 0rem 1.5em;
  width: 90%; }
  .modal-facts p {
    margin: .3rem 0; }
  .modal-facts span {
    color: #869cb2; }

.modal-footer {
  font-size: 2rem;
  background-color: #202932;
  background: linear-gradient(to bottom, #2d4358 0%, #205a65 100%);
  color: #e3e8ed;
  border-top: 1px solid #485c70;
  border-bottom-right-radius: 0.5rem; }
  .modal-footer img {
    margin-left: 1rem;
    height: 2.75rem;
    border-radius: 50%; }
  .modal-footer .modal-imdb-rating, .modal-footer .modal-metacritic-rating {
    margin: 1rem 1.5rem; }

.modal-ratings {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto; }

.container {
  display: block;
  margin: 2rem 2rem;
  text-align: center; }

.filters {
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  float: left;
  top: 0;
  left: 0;
  width: 350px;
  height: 100%;
  padding: 2rem;
  background: #202932;
  /* Old browsers */
  background: linear-gradient(to bottom, #2d4358 0%, #205a65 100%); }
  .filters h2 {
    font-weight: normal;
    font-size: 1rem; }

.filters-container {
  width: 90%; }

.flex-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-left: 390px; }

.loading {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-left: 390px; }

.spinner {
  display: none;
  width: 8rem;
  height: 8rem;
  background: #2d4358;
  background: linear-gradient(to top, #2d4358 0%, #205a65 100%);
  background-size: 200% 200%;
  margin: 5rem auto;
  animation: sk-rotateplane 2s infinite ease-in-out; }

@keyframes sk-rotateplane {
  0% {
    transform: rotateZ(0deg) scale(0.65); }
  25% {
    transform: rotateZ(90deg) scale(1); }
  50% {
    transform: rotateZ(180deg) scale(0.65); }
  75% {
    transform: rotateZ(270deg) scale(1); }
  100% {
    transform: rotateZ(360deg) scale(0.65); } }

.genres {
  width: 100%; }

.checkbox {
  font-size: .8rem;
  display: inline-block;
  background-color: #485c70;
  border-radius: .5rem;
  padding: .45rem .55rem .5rem .55rem;
  margin: .2rem; }

.checkbox:hover {
  background-color: #344351;
  cursor: pointer; }

.checkbox-selected {
  background-color: #d4dce3;
  color: #202932; }

.checkbox-selected:hover {
  background-color: #eee; }

.sliders {
  width: 100%;
  text-align: left; }

.slider {
  min-width: 266.68px;
  margin: 0 auto;
  margin-bottom: 4rem; }

.slider-label {
  margin: 1rem 0 2.5rem 0; }

.noUi-tooltip {
  border: 0;
  border-radius: .5rem;
  text-align: center;
  background: #485c70;
  color: #eee;
  font-size: 1rem;
  padding: .2rem .5rem .3rem .5rem; }

.noUi-target {
  background: #202932;
  border: 0;
  box-shadow: none;
  top: 0.25rem;
  height: 0.5rem; }

.noUi-base {
  border: 0;
  top: -0.5rem;
  height: 1.5rem; }

.noUi-origin {
  margin-top: 0.5rem; }

.noUi-handle {
  background-color: #eee;
  box-shadow: none;
  border: 0;
  border-radius: 50%;
  width: 1.5rem !important;
  height: 1.5rem !important;
  left: 0 !important;
  top: -0.5rem !important; }

.noUi-connect {
  box-shadow: none;
  background-color: #485c70;
  height: 0.5rem;
  margin-top: 0.5rem; }

.noUi-handle:before,
.noUi-handle:after {
  display: none; }

.noUi-horizontal.noUi-extended {
  padding-right: 16px;
  /* was 32 */ }

.noUi-horizontal.noUi-extended .noUi-handle {
  left: -1px; }

.noUi-horizontal.noUi-extended .noUi-origin {
  right: -32px; }

.card {
  margin: 1.75rem 1.75rem;
  display: flex;
  flex-direction: row;
  width: 266.68px !important;
  height: 400px;
  border-radius: 0.5rem;
  background-size: cover;
  background-position: center center;
  background-color: #202932;
  box-shadow: 0px 3px 20px 0px rgba(32, 41, 50, 0.4); }

.card-menu {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 400px;
  width: 100%;
  color: #eee;
  user-select: none;
  cursor: default;
  border-radius: 0.5rem; }

.info-button {
  font-size: 1.3rem;
  align-items: flex-start;
  background-color: #202932;
  background: linear-gradient(to top, #2d4358 0%, #205a65 100%);
  text-transform: uppercase;
  padding: .5rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
  cursor: pointer; }

.card-trailer {
  align-items: center;
  height: 110px;
  width: 50%;
  margin: 0 auto;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: height .2s; }

.card-trailer:hover {
  height: 130px;
  cursor: pointer; }

.ratings {
  display: flex;
  justify-content: center;
  flex-direction: row;
  font-size: 1.3rem;
  background-color: #202932;
  background: linear-gradient(to bottom, #2d4358 0%, #205a65 100%);
  height: 3rem;
  line-height: 3rem;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem; }
  .ratings img {
    height: 2rem;
    margin-top: .5rem;
    margin-right: 1rem;
    border-radius: 50%; }
  .ratings .imdb-rating {
    margin-right: 2rem; }

.imdb-rating {
  align-items: flex-start; }

.tomato-rating {
  align-items: flex-end; }
